import { Inject, Injectable } from '@nestjs/common';
import { PandaAndroidService } from './panda-android.service';
import { PandaIosService } from './panda-ios.service';
import { PANDA_SERVICE_ANDROID, PANDA_SERVICE_IOS } from './constants/websocket';
import { IWebSocketService } from './interface/websocket.interface';
import { PandaDeviceType } from './constants/device';
import { IPandaDevice } from './interface/device';

@Injectable()
export class PandaManagerService {
  private readonly pandaServices: Map<string, IWebSocketService> = new Map();

  constructor(
    @Inject(PANDA_SERVICE_ANDROID)
    private readonly pandaAndroidService: PandaAndroidService,
    @Inject(PANDA_SERVICE_IOS)
    private readonly pandaIosService: PandaIosService,
  ) {
    this.registerPandaService(PandaDeviceType.ANDROID, this.pandaAndroidService);
    this.registerPandaService(PandaDeviceType.IOS, this.pandaIosService);
  }

  /**
   * Register a panda service
   */
  private registerPandaService(deviceType: PandaDeviceType, service: IWebSocketService): void {
    this.pandaServices.set(deviceType, service);
  }

  getPandaService(deviceType: PandaDeviceType): IWebSocketService {
    return this.pandaServices.get(deviceType);
  }

  isConnected(): boolean {
    return this.pandaAndroidService.isConnectedStatus() || this.pandaIosService.isConnectedStatus();
  }

  isAndroidConnected(): boolean {
    return this.pandaAndroidService.isConnectedStatus();
  }

  isIosConnected(): boolean {
    return this.pandaIosService.isConnectedStatus();
  }

  /**
   * Get all devices from both Android and iOS
   */
  async getDevices(deviceType: PandaDeviceType = PandaDeviceType.ALL) {
    let androidDevices: Array<IPandaDevice> = [];
    let iosDevices: Array<IPandaDevice> = [];

    switch (deviceType) {
      case PandaDeviceType.ALL:
        if (this.pandaAndroidService.isConnectedStatus()) {
          androidDevices = await this.pandaAndroidService.listDevices();
        }

        if (this.pandaIosService.isConnectedStatus()) {
          iosDevices = await this.pandaIosService.listDevices();
        }

        break;

      case PandaDeviceType.ANDROID:
        if (this.pandaAndroidService.isConnectedStatus()) {
          androidDevices = await this.pandaAndroidService.listDevices();
        }
        
        break;

      case PandaDeviceType.IOS:
        if (this.pandaIosService.isConnectedStatus()) {
          iosDevices = await this.pandaIosService.listDevices();
        }
        break;

      default:
        return [];
    }

    return [...androidDevices, ...iosDevices];
  }
}