import { Injectable, Logger } from '@nestjs/common';
import { Cron, CronExpression } from '@nestjs/schedule';
import { Job, Queue } from 'bull';
import { DeviceService } from '../device/device.service';
import { DeviceLockService } from '../device/device-lock.service';
import { InternalTransfer } from '../../common/interfaces/withdrawal.interface';
import { WithdrawalProcessingError, ErrorType } from './withdrawal-errors';
import { cloneDeep } from 'lodash';
import { withCronLock } from '@/common/redis/cron-lock';
import { BankService } from '../bank/bank.service';
import { TelegramService } from '../notifiction/telegram.service';
import { sleep } from '@/common/utils/time';
import { DeviceConfig } from '../device/dto/device-config.dto';
import { validateObjectResult } from '@/common/utils/object-validation';
import { RedisService } from '@/common/services/redis.service';
import { DELAY_RETRY_JOB, JOB_PRIORITY_KEY_INTERNAL_JOB, JOB_PROCESS_NAME_INTERNAL_JOB } from './constants/job';
import { WithdrawalStatus } from './constants/withdrawal-status';
import { formatCurrency } from '@/common/utils/currency';
import { PandaManagerService } from '@/common/modules/websocket/panda-manager.service';
import { bullQueueNames } from './constants/queue';
import { InjectQueue } from '@nestjs/bull';
import { InternalTransferService } from './internal-transfer.service';
import { EzpayBeClientService } from '../notifiction/ezpay-be-client.service';

@Injectable()
export class InternalTransferJobManagerService {
  private readonly logger = new Logger('InternalTransferJobManagerService');
  private processingJobs: Set<number | string> = new Set();

  constructor(
    @InjectQueue(bullQueueNames.INTERNAL_TRANSFER)
    private readonly internalTransferQueue: Queue<InternalTransfer>,
    private readonly deviceService: DeviceService,
    private readonly deviceLockService: DeviceLockService,
    private readonly pandaManagerService: PandaManagerService,
    private readonly bankService: BankService,
    private readonly ezpayBeClient: EzpayBeClientService,
    private readonly telegramService: TelegramService,
    private readonly redisService: RedisService,
    private readonly internalTransferService: InternalTransferService,
  ) { }

  /**
   * Pick and process jobs from queue
   * Strategy: Find N available devices -> Pick N jobs -> Assign strictly
   */
  async pickAndProcessJobs(): Promise<void> {
    try {
      // 1. Get Available Devices (Active + Unlocked)
      const availableBankInDevices = await this.deviceService.getBankInAvailableDevices();

      // Calculate capacity
      const capacity = availableBankInDevices.length;

      if (capacity <= 0) {
        return;
      }

      // Get job with priority from bull queue
      const waitingJobsWithScore = await this.redisService.pickFromZSet(JOB_PRIORITY_KEY_INTERNAL_JOB, capacity);

      if (waitingJobsWithScore.length === 0) {
        return;
      }

      // 3. Assign Job -> Device
      // We iterate through picked jobs and assign to available devices index-by-index
      for (let i = 0; i < waitingJobsWithScore.length; i++) {
        const jobId = waitingJobsWithScore[i].jobId;
        const job = await this.internalTransferQueue.getJob(jobId);
        const device = availableBankInDevices[i]; // Guaranteed to exist because jobsToPick <= capacity
        const deviceName = `${device.bankCode} - ${device.transferType} - ${device.accountName}`;

        // Skip if already processing (safety check)
        if (this.processingJobs.has(job.id)) {
          continue;
        }

        // CRITICAL: Acquire lock immediately to prevent race condition
        const operationId = `internal-transfer:${job.data.withdrawalId}:${device.deviceId}`;
        const lockAcquired = await this.deviceLockService.acquireLock(
          device.deviceId,
          'internal-transfer',
          operationId,
          3.68 * 60 * 1000 // 3.68 minutes
        );

        if (!lockAcquired) {
          this.logger.warn(`Device ${device.deviceId} was locked after assignment, skipping job ${job.id}`);
          continue;
        }

        const internalTransfer = cloneDeep(job.data);
        const jobValidated = await validateObjectResult(InternalTransfer, internalTransfer);

        if (!jobValidated.ok) {
          this.logger.error(`Invalid Internal Transfer: missing ${jobValidated.errors.map(error => error.property).join(', ')}`);

          this.ezpayBeClient
            .updateInternalTransfer(internalTransfer._id, {
              status: WithdrawalStatus.FAILED
            })
            .catch((error) => {
              this.logger.error(`Error updating internal transfer status for ${internalTransfer.withdrawalId}: ${error.message}`, error.stack);
            });

          this.telegramService.sendMessage({
            text: ` <b>Failed Internal Transfer. Retrying...</b>`
              + `\n${internalTransfer.withdrawalId}`
              + `\n${internalTransfer.withdrawalCode}`
              + `\nFailed: Thng tin job khng hp l`
              + `\n\n<b>To:</b> ${internalTransfer.bankCode} - ${internalTransfer.beneficiaryAccountNo} - ${internalTransfer.beneficiaryName}`
              + `\n<b>Amount:</b>  ${formatCurrency(internalTransfer.amount)}`
              + `\n<b>Description:</b>  ${internalTransfer.description}`,
            options: {
              parseMode: 'HTML'
            }
          }).catch((error) => {
            this.logger.error(`Error sending message for withdrawal ${internalTransfer.withdrawalId}: ${error.message}`, error.stack);
          });

          await job.remove();

          continue
        }

        this.logger.log(
          `Found job ${internalTransfer.withdrawalId} is internal transfer job. `
          + `Assigned to ${deviceName}`
        );

        this.processingJobs.add(job.id);
        await job.remove();

        // Lock acquired - process job
        // Note: processInternalTransfer will release the lock in its finally block
        this.processJob(internalTransfer, device)
          .catch(error => {
            this.logger.error(`Error processing job ${job.id}: ${error.message}`, error.stack);
          })
          .finally(() => {
            // Safety: release lock if processJob fails before processInternalTransfer
            this.deviceLockService.releaseLock(device.deviceId).catch(() => { });
            this.processingJobs.delete(job.id);
          });
      }
    } catch (error) {
      this.logger.error(`Error picking jobs: ${error.message}`, error.stack);
    }
  }

  /**
   * Process a single job with assigned device
   */
  private async processJob(internalTransfer: InternalTransfer, deviceConfig: DeviceConfig): Promise<void> {
    try {
      const { _id, withdrawalId } = internalTransfer;
      const internalTransferDetails = await this.internalTransferService.getInternalTransferDetails(_id);

      if (![WithdrawalStatus.PENDING, WithdrawalStatus.RETRY].includes(internalTransferDetails.status)) {
        this.logger.warn(`Internal transfer ${withdrawalId} (${internalTransferDetails.status}) is not pending or retry, skipping`);

        throw new WithdrawalProcessingError(`Internal transfer not pending or retry, skipping (${internalTransferDetails.status})`, ErrorType.PERMANENT);
      }

      if (internalTransferDetails.status === WithdrawalStatus.RETRY) {
        this.telegramService.sendMessage({
          text: ` <b>Retrying Internal Transfer...</b>`
            + `\n${internalTransfer.withdrawalId} - ${internalTransfer.withdrawalCode}`
            + `\n\n<b>From:</b>  ${deviceConfig.bankCode} - ${deviceConfig.accountName}`
            + `\n<b>To:</b> ${internalTransfer.bankCode} - ${internalTransfer.beneficiaryAccountNo} - ${internalTransfer.beneficiaryName}`
            + `\n<b>Amount:</b>  ${formatCurrency(internalTransfer.amount)}`
            + `\n<b>Description:</b>  ${internalTransfer.description}`,
          options: {
            parseMode: 'HTML'
          }
        }).catch((error) => {
          this.logger.error(`Error sending message for internal transfer ${internalTransfer.withdrawalId}: ${error.message}`, error.stack);
        });
      }

      this.ezpayBeClient
        .updateInternalTransfer(_id, {
          status: WithdrawalStatus.PROCESSING,
        })
        .catch((error) => {
          this.logger.error(`Error updating internal transfer status for ${withdrawalId}: ${error.message}`, error.stack);
        });

      await this.bankService.createQRCodeAndTransferTo(deviceConfig.bankCode, deviceConfig.deviceId, internalTransfer);
      await this.bankService.createBillFolder(deviceConfig.bankCode, internalTransfer.withdrawalCode);
      await this.internalTransferService.processInternalTransfer(internalTransfer, deviceConfig);
    } catch (error) {
      // Extract error type t error nu c
      let errorType: ErrorType;
      let errorContext: string;

      if (error instanceof WithdrawalProcessingError) {
        errorType = error.errorType;
        errorContext = error.context;
        this.logger.error(`Controlled Error ${internalTransfer.withdrawalId}: ${errorType}-${error.message}`, error.stack);

        await this.handleJobFailure(deviceConfig, internalTransfer, error.message, errorType, errorContext);
      } else {
        this.logger.error(`Uncontrolled Error ${internalTransfer.withdrawalId}: ${errorType}-${error.message}`, error.stack);

        await this.bankService.createBillFolder(deviceConfig.bankCode, internalTransfer.withdrawalCode);
        await sleep(1000);
        await this.bankService.captureScreen(deviceConfig.bankCode, deviceConfig.deviceId, internalTransfer.withdrawalCode);
        const billImagePath = await this.bankService.getBillImagePath(deviceConfig.bankCode, internalTransfer.withdrawalCode);

        if (billImagePath) {
          this.logger.log(`Uploading receipt for internal transfer ${internalTransfer.withdrawalCode} - ${billImagePath}`);

          await this.ezpayBeClient.uploadInternalTransferReceipt(internalTransfer._id, billImagePath);
        }

        await this.ezpayBeClient.updateInternalTransfer(internalTransfer._id, {
          status: WithdrawalStatus.UNKNOWN
        });

        this.telegramService.sendMessage({
          text: ` <b>Failed Internal Transfer.</b>`
            + `\n${internalTransfer.withdrawalId}`
            + `\n${internalTransfer.withdrawalCode}`
            + `\n${error.message}`
            + `\n\n<b>From:</b>  ${deviceConfig.bankCode} - ${deviceConfig.accountName}`
            + `\n<b>To:</b> ${internalTransfer.bankCode} - ${internalTransfer.beneficiaryAccountNo} - ${internalTransfer.beneficiaryName}`
            + `\n<b>Amount:</b>  ${formatCurrency(internalTransfer.amount)}`
            + `\n<b>Description:</b>  ${internalTransfer.description}`,
          options: {
            parseMode: 'HTML'
          }
        }).catch((error) => {
          this.logger.error(`Error sending message for withdrawal ${internalTransfer.withdrawalId}: ${error.message}`, error.stack);
        });

        await this.bankService.login(deviceConfig.bankCode, deviceConfig.deviceId, true);
      }
    }
  }

  /**
   * Handle Job Failure: Retry logic with error type classification
   */
  private async handleJobFailure(
    deviceConfig: DeviceConfig,
    internalTransfer: InternalTransfer,
    reason: string,
    errorType: ErrorType = ErrorType.PERMANENT,
    errorContext: string = ''
  ) {
    const maxRetries = 2;
    const currentRetries = internalTransfer.manualRetryCount ?? 1;

    switch (errorType) {
      case ErrorType.PERMANENT:
        this.ezpayBeClient
          .updateInternalTransfer(internalTransfer._id, {
            status: WithdrawalStatus.FAILED
          })
          .catch((error) => {
            this.logger.error(`Error updating withdrawal status for ${internalTransfer.withdrawalId}: ${error.message}`, error.stack);
          });

        this.telegramService.sendMessage({
          text: ` <b>Failed Internal Transfer. Retrying...</b>`
            + `\n${internalTransfer.withdrawalId}`
            + `\n${internalTransfer.withdrawalCode}`
            + `\n${errorContext}`
            + `\n\n<b>From:</b>  ${deviceConfig.bankCode} - ${deviceConfig.accountName}`
            + `\n<b>To:</b> ${internalTransfer.bankCode} - ${internalTransfer.beneficiaryAccountNo} - ${internalTransfer.beneficiaryName}`
            + `\n<b>Amount:</b>  ${formatCurrency(internalTransfer.amount)}`
            + `\n<b>Description:</b>  ${internalTransfer.description}`,
          options: {
            parseMode: 'HTML'
          }
        }).catch((error) => {
          this.logger.error(`Error sending message for withdrawal ${internalTransfer.withdrawalId}: ${error.message}`, error.stack);
        });

        await this.bankService.login(deviceConfig.bankCode, deviceConfig.deviceId, true);

        return;

      case ErrorType.FORCE_LOGIN_NO_REQUEUE:
        await this.bankService.login(deviceConfig.bankCode, deviceConfig.deviceId, true);

        return;

      case ErrorType.NO_BILL_AFTER_OTP:
        await this.bankService.captureScreen(deviceConfig.bankCode, deviceConfig.deviceId, internalTransfer.withdrawalCode);
        const billImagePath = await this.bankService.getBillImagePath(deviceConfig.bankCode, internalTransfer.withdrawalCode);

        if (billImagePath) {
          this.logger.log(`Uploading receipt for internal transfer ${internalTransfer.withdrawalCode} - ${billImagePath}`);

          await this.ezpayBeClient.uploadInternalTransferReceipt(internalTransfer._id, billImagePath);
        }

        await this.ezpayBeClient.updateInternalTransfer(internalTransfer._id, {
          status: WithdrawalStatus.UNKNOWN
        });
        await this.bankService.login(deviceConfig.bankCode, deviceConfig.deviceId, true);

        return;

      case ErrorType.FORCE_LOGIN_AND_REQUEUE: // Force Login s cho retry nn khng cn break
        this.logger.warn(
          `Added ${internalTransfer.withdrawalId} to queue for retrying. (${currentRetries}/${maxRetries})` +
          `Reason: ${reason}. ` +
          `Error Type: ${errorType}.`
        );

        if (currentRetries < maxRetries) {

          await this.internalTransferQueue.add(
            JOB_PROCESS_NAME_INTERNAL_JOB,
            {
              ...internalTransfer,
              manualRetryCount: currentRetries + 1,
              lastErrorType: errorType,
              lastErrorReason: reason
            },
            {
              jobId: `int-${internalTransfer.withdrawalId}`,
              priority: 1,
              delay: DELAY_RETRY_JOB,
            }
          );

          this.ezpayBeClient
            .updateInternalTransfer(internalTransfer._id, {
              status: WithdrawalStatus.RETRY
            })
            .catch((error) => {
              this.logger.error(`Error updating withdrawal status for ${internalTransfer.withdrawalId}: ${error.message}`, error.stack);
            });

          await this.bankService.captureScreen(deviceConfig.bankCode, deviceConfig.deviceId, internalTransfer.withdrawalCode);
          const billImagePath = await this.bankService.getBillImagePath(deviceConfig.bankCode, internalTransfer.withdrawalCode);

          const caption = ` <b>Failed Internal Transfer. Retrying...</b>`
            + `\n${internalTransfer.withdrawalId}`
            + `\n${internalTransfer.withdrawalCode}`
            + `\n${errorContext}`
            + `\n\n<b>From:</b>  ${deviceConfig.bankCode} - ${deviceConfig.accountName}`
            + `\n<b>To:</b> ${internalTransfer.bankCode} - ${internalTransfer.beneficiaryAccountNo} - ${internalTransfer.beneficiaryName}`
            + `\n<b>Amount:</b>  ${formatCurrency(internalTransfer.amount)}`
            + `\n<b>Description:</b>  ${internalTransfer.description}`;

          await this.telegramService.sendPhotoByFile({
            filePath: billImagePath,
            options: {
              parseMode: 'HTML',
              caption
            }
          });
        } else {
          this.logger.warn(`Max retries (${maxRetries}) exceeded for internal transfer ${internalTransfer.withdrawalId}: ${errorType} error: ${reason}`);

          this.ezpayBeClient
            .updateInternalTransfer(internalTransfer._id, {
              status: WithdrawalStatus.FAILED
            })
            .catch((error) => {
              this.logger.error(`Error updating internal transfer status for ${internalTransfer.withdrawalId}: ${error.message}`, error.stack);
            });

          await this.bankService.captureScreen(deviceConfig.bankCode, deviceConfig.deviceId, internalTransfer.withdrawalCode);
          const billImagePath = await this.bankService.getBillImagePath(deviceConfig.bankCode, internalTransfer.withdrawalCode);

          const caption = ` <b>Failed Internal Transfer. Max retries exceeded...</b>`
            + `\n${internalTransfer.withdrawalId}`
            + `\n${internalTransfer.withdrawalCode}`
            + `\n${errorContext}`
            + `\n\n<b>From:</b>  ${deviceConfig.bankCode} - ${deviceConfig.accountName}`
            + `\n<b>To:</b> ${internalTransfer.bankCode} - ${internalTransfer.beneficiaryAccountNo} - ${internalTransfer.beneficiaryName}`
            + `\n<b>Amount:</b>  ${formatCurrency(internalTransfer.amount)}`
            + `\n<b>Description:</b>  ${internalTransfer.description}`;

          await this.telegramService.sendPhotoByFile({
            filePath: billImagePath,
            options: {
              parseMode: 'HTML',
              caption
            }
          });
        }

        await this.bankService.login(deviceConfig.bankCode, deviceConfig.deviceId, true);
        break;

      case ErrorType.TEMPORARY:
        this.logger.warn(
          `Added ${internalTransfer.withdrawalId} to queue for retrying. (${currentRetries}/${maxRetries})` +
          `Reason: ${reason}. ` +
          `Error Type: ${errorType}.`
        );

        if (currentRetries < maxRetries) {

          await this.internalTransferQueue.add(
            JOB_PROCESS_NAME_INTERNAL_JOB,
            {
              ...internalTransfer,
              manualRetryCount: currentRetries + 1,
              lastErrorType: errorType,
              lastErrorReason: reason
            },
            {
              jobId: `int-${internalTransfer.withdrawalId}`,
              priority: 1,
              delay: DELAY_RETRY_JOB,
            }
          );

          this.ezpayBeClient
            .updateInternalTransfer(internalTransfer._id, {
              status: WithdrawalStatus.RETRY
            })
            .catch((error) => {
              this.logger.error(`Error updating withdrawal status for ${internalTransfer.withdrawalId}: ${error.message}`, error.stack);
            });

          this.telegramService.sendMessage({
            text: ` <b>Failed Internal Transfer. Retrying...</b>`
              + `\n${internalTransfer.withdrawalId}`
              + `\n${internalTransfer.withdrawalCode}`
              + `\n${errorContext}`
              + `\n\n<b>From:</b>  ${deviceConfig.bankCode} - ${deviceConfig.accountName}`
              + `\n<b>To:</b> ${internalTransfer.bankCode} - ${internalTransfer.beneficiaryAccountNo} - ${internalTransfer.beneficiaryName}`
              + `\n<b>Amount:</b>  ${formatCurrency(internalTransfer.amount)}`
              + `\n<b>Description:</b>  ${internalTransfer.description}`,
            options: {
              parseMode: 'HTML'
            }
          }).catch((error) => {
            this.logger.error(`Error sending message for internal transfer ${internalTransfer.withdrawalId}: ${error.message}`, error.stack);
          });

        } else {
          this.logger.warn(`Max retries (${maxRetries}) exceeded for internal transfer ${internalTransfer.withdrawalId}: ${errorType} error: ${reason}`);

          this.ezpayBeClient
            .updateInternalTransfer(internalTransfer._id, {
              status: WithdrawalStatus.FAILED
            })
            .catch((error) => {
              this.logger.error(`Error updating internal transfer status for ${internalTransfer.withdrawalId}: ${error.message}`, error.stack);
            });

          await this.bankService.captureScreen(deviceConfig.bankCode, deviceConfig.deviceId, internalTransfer.withdrawalCode);
          const billImagePath = await this.bankService.getBillImagePath(deviceConfig.bankCode, internalTransfer.withdrawalCode);

          const caption = ` <b>Failed Internal Transfer. Max retries exceeded...</b>`
            + `\n${internalTransfer.withdrawalId}`
            + `\n${internalTransfer.withdrawalCode}`
            + `\n${errorContext}`
            + `\n\n<b>From:</b>  ${deviceConfig.bankCode} - ${deviceConfig.accountName}`
            + `\n<b>To:</b> ${internalTransfer.bankCode} - ${internalTransfer.beneficiaryAccountNo} - ${internalTransfer.beneficiaryName}`
            + `\n<b>Amount:</b>  ${formatCurrency(internalTransfer.amount)}`
            + `\n<b>Description:</b>  ${internalTransfer.description}`

          await this.telegramService.sendPhotoByFile({
            filePath: billImagePath,
            options: {
              parseMode: 'HTML',
              caption
            }
          });
        }
        break;
    }
  }

  private async promoteDelayedJobs() {
    return withCronLock('cron:promote_delayed:internal-transfer', 5000, async () => {
      const now = Date.now();
      const BATCH = 100;

      // Ly mt batch job  delayed
      const delayedJobs: Job[] = await this.internalTransferQueue.getJobs(
        ['delayed'],
        0,
        BATCH - 1,
      );

      if (!delayedJobs.length) return;

      let promoted = 0;

      for (const job of delayedJobs) {
        const delayMs = typeof job.opts?.delay === 'number' ? job.opts.delay : 0;
        const ts = typeof job.timestamp === 'number' ? job.timestamp : 0;

        const dueAt = ts + delayMs;

        // ch promote job n hn
        if (dueAt <= now) {
          try {
            // Bull h tr job.promote()  chuyn delayed -> waiting
            await job.promote();
            promoted++;
          } catch (e) {
            this.logger.debug(`Promote skipped for job ${job.id}: ${String(e)}`);
          }
        }
      }

      if (promoted > 0) {
        this.logger.log(`Promoted ${promoted} delayed jobs to waiting`);
      }
    });
  }

  /**
   * Periodically check and pick jobs if there are available slots
   * Runs every 5 seconds
   */
  @Cron(CronExpression.EVERY_5_SECONDS)
  async periodicJobPicking() {
    if (!this.pandaManagerService.isConnected()) {
      this.logger.error(`Panda is not connected, cannot pick jobs`);

      return;
    }

    // Check service status
    const serviceStatus = await this.deviceService.getServiceStatus();
    if (serviceStatus !== 'active') {
      this.logger.debug(`Service is ${serviceStatus}, cannot pick jobs`);
      return;
    }

    await this.pickAndProcessJobs();
  }

  /**
   * Promote delayed jobs to waiting jobs
   * Runs every 5 seconds
   */
  @Cron(CronExpression.EVERY_5_SECONDS)
  async promoteDelayedJobsPeriodically() {
    await this.promoteDelayedJobs();
  }
}
