import { Module, forwardRef } from '@nestjs/common';
import { BullModule } from '@nestjs/bull';
import { ScheduleModule } from '@nestjs/schedule';
import { WithdrawalService } from './withdrawal.service';
import { WithdrawalJobManagerService } from './withdrawal-job-manager.service';
import { WithdrawalController } from './withdrawal.controller';
import { EzpayBeClientService } from '../notifiction/ezpay-be-client.service';
import { RedisService } from '../../common/services/redis.service';
import { DeviceModule } from '../device/device.module';
import { BankModule } from '../bank/bank.module';
import { HttpModule } from '@nestjs/axios';
import { WindowAppModule } from '../window-app/window-app.module';
import { bullQueueNames } from './constants/queue';
import { InternalTransferService } from './internal-transfer.service';
import { InternalTransferJobManagerService } from './internal-job-manager.service';

@Module({
  imports: [
    BullModule.registerQueue(
      {
        name: bullQueueNames.WITHDRAWAL_REQUEST, // Single queue - jobs c pick th cng,
        defaultJobOptions: {
          attempts: 0,
          removeOnComplete: false,
          removeOnFail: false
        },
      },
      {
        name: bullQueueNames.WITHDRAWAL_REQUEST_LARGE_AMOUNT,
        defaultJobOptions: {
          attempts: 0,
          removeOnComplete: false,
          removeOnFail: false
        },
      },
      {
        name: bullQueueNames.INTERNAL_TRANSFER,
        defaultJobOptions: {
          attempts: 0,
          removeOnComplete: false,
          removeOnFail: false
        },
      }
    ),
    ScheduleModule,
    forwardRef(() => DeviceModule),
    BankModule,
    HttpModule,
    WindowAppModule
  ],
  controllers: [WithdrawalController],
  providers: [
    WithdrawalService,
    WithdrawalJobManagerService,
    EzpayBeClientService,
    RedisService,
    InternalTransferService,
    InternalTransferJobManagerService
  ],
  exports: [WithdrawalService],
})
export class WithdrawalModule {}

