import { Global, Module } from '@nestjs/common';
import { PANDA_SERVICE_ANDROID, PANDA_SERVICE_IOS } from './constants/websocket';
import { RedisService } from '@/common/services/redis.service';
import { PandaAndroidService } from './panda-android.service';
import { PandaIosService } from './panda-ios.service';
import { PandaManagerService } from './panda-manager.service';

@Global()
@Module({
  providers: [
    RedisService,
    {
      provide: PANDA_SERVICE_ANDROID,
      useClass: PandaAndroidService,
    },
    {
      provide: PANDA_SERVICE_IOS,
      useClass: PandaIosService
    },
    PandaManagerService
  ],
  exports: [PandaManagerService, PANDA_SERVICE_ANDROID, PANDA_SERVICE_IOS],
})
export class WebSocketModule {}

