import { Withdrawal } from "@/common";
import { DeviceConfig } from "@/modules/device/dto/device-config.dto";
import { Job } from "bull";

export const sortDevices = (
  devices: DeviceConfig[],
  key: 'minTransferAmount',
  direction: 'asc' | 'desc'
): DeviceConfig[] => {
  if (direction === 'asc') {
    return [...devices].sort((a, b) => a[key] - b[key]);
  } else {
    return [...devices].sort((a, b) => b[key] - a[key]);
  }
}

export const sortJobs = (
  jobs: Job<Withdrawal>[],
  key: 'priority',
  direction: 'asc' | 'desc'
): Job<Withdrawal>[] => {
  if (direction === 'asc') {
    return jobs.sort((a, b) => a.data[key] - b.data[key]);
  } else {
    return jobs.sort((a, b) => b.data[key] - a.data[key]);
  }
}