import { Injectable, Logger } from '@nestjs/common';
import * as sharp from 'sharp';
import { ROI } from './image-preprocessing.service';

export interface RGB {
  r: number;
  g: number;
  b: number;
}

export interface BlueButtonThreshold {
  /** Red must be below this (blue has low red). Default: 150 */
  maxR?: number;
  /** Green must be above this. Default: 80 */
  minG?: number;
  /** Blue must be above this. Default: 120 */
  minB?: number;
  /** Blue should dominate: B > R. Default: true */
  blueDominates?: boolean;
}

const DEFAULT_BLUE_THRESHOLD: Required<BlueButtonThreshold> = {
  maxR: 150,
  minG: 80,
  minB: 120,
  blueDominates: true,
};

@Injectable()
export class ColorDetectionService {
  private readonly logger = new Logger(ColorDetectionService.name);

  /**
   * Get mean RGB color of a region from image file
   */
  async getMeanColor(imagePath: string, roi: ROI): Promise<RGB | null> {
    try {
      const image = sharp(imagePath);
      const metadata = await image.metadata();
      const width = metadata.width || 0;
      const height = metadata.height || 0;

      if (width === 0 || height === 0) return null;

      const cropX = Math.floor(roi.x * width);
      const cropY = Math.floor(roi.y * height);
      const cropWidth = Math.max(10, Math.floor(roi.width * width));
      const cropHeight = Math.max(10, Math.floor(roi.height * height));

      const { data, info } = await image
        .extract({
          left: Math.min(cropX, width - cropWidth),
          top: Math.min(cropY, height - cropHeight),
          width: cropWidth,
          height: cropHeight,
        })
        .raw()
        .toBuffer({ resolveWithObject: true });

      const channels = info.channels;
      let r = 0;
      let g = 0;
      let b = 0;
      let count = 0;

      for (let i = 0; i < data.length; i += channels) {
        r += data[i];
        g += data[i + 1];
        b += data[i + 2];
        count++;
      }

      if (count === 0) return null;

      return {
        r: Math.round(r / count),
        g: Math.round(g / count),
        b: Math.round(b / count),
      };
    } catch (error) {
      this.logger.error(`getMeanColor error: ${error.message}`, error.stack);
      return null;
    }
  }

  /**
   * Check if color matches blue button criteria
   */
  isBlue(color: RGB, threshold: BlueButtonThreshold = {}): boolean {
    const t = { ...DEFAULT_BLUE_THRESHOLD, ...threshold };
    if (color.r > t.maxR) return false;
    if (color.g < t.minG) return false;
    if (color.b < t.minB) return false;
    if (t.blueDominates && color.b <= color.r) return false;
    return true;
  }

  /**
   * Check if image region is blue (convenience method)
   */
  async isRegionBlue(
    imagePath: string,
    roi: ROI,
    threshold?: BlueButtonThreshold,
  ): Promise<boolean> {
    const color = await this.getMeanColor(imagePath, roi);
    if (!color) {
      this.logger.debug(`[ColorDetection] getMeanColor returned null`);
      return false;
    }
    const result = this.isBlue(color, threshold);
    this.logger.debug(
      `[ColorDetection] ROI mean RGB: R=${color.r} G=${color.g} B=${color.b} => isBlue=${result}`,
    );
    return result;
  }
}
